import React, { useEffect, useLayoutEffect, useState } from 'react';
import mermaid from 'mermaid';
import buildMermaidStateDiagramString from '../logic/stateDiagramBuilder';

function MermaidChart({ currentState, stateJson, currentIndex, stateLogs }) {
  const [svg, setSvg] = useState('');
  const [mermaidString, setMermaidString] = useState('');

// json
  useEffect(()=>{
    if (stateJson) {
      setMermaidString(buildMermaidStateDiagramString(stateJson, currentState, currentIndex, stateLogs));
      setSvg(''); // Reset SVG when stateJson changes
    }
  }, [stateJson]);



  useLayoutEffect(() => {
    // currentStatemermaidString
    if (!currentState || !mermaidString) {
      return;
    }

    const id = 'mermaid-' + Math.floor(Math.random() * 100000);
    mermaid.initialize({ startOnLoad: false });
    if(!stateJson) return;
    mermaid.render(id, buildMermaidStateDiagramString(stateJson, currentState, currentIndex, stateLogs))
      .then(({ svg }) => setSvg(svg))
      .catch(err => console.error('<Mermaid rendering error>:', err));
  }, [currentIndex, currentState, mermaidString]);


  return (
    <div className='card' style={{minHeight: '300px'}}>
      <div className='card-header'> State Diagram</div>
      <div className="card-body">
        <div style={{ minHeight: '200px' }}
          dangerouslySetInnerHTML={{ __html: svg }}
        />        
      </div>
    </div>
  );
}

export default MermaidChart;
