// serial/serialIO.js
// 
// - Serial 
// - reader 
// -  parse  callback 

import { parseLine } from "../log/parseLine";

const readerMap = new Map();

/**
 * 
 *
 * @param {SerialPort} port - 
 * @param {string} deviceId - 
 * @param {(deviceId: string, parsed: {kind: string, payload: any}) => void} onParsed - 
 * @param {(deviceId: string, status: "open" | "error") => void} onStatus - 
 * @param {(deviceId: string, type: "connected" | "disconnected" | "open_error" | "close_error" | "read_error") => void} onSystemLog - 
 */
export const openPort = async (port, deviceId, onParsed, onStatus, onSystemLog) => {
  try {
    await port.open({ baudRate: 115200 });
    onStatus(deviceId, "open");
    //  system 
    onSystemLog(deviceId, "connected");

    startReaderTop(port, deviceId, onParsed, onStatus, onSystemLog);

  } catch (e) {
    onStatus(deviceId, "error");
    onSystemLog(deviceId, "open_error");
  }
};

/**
 * 
 *
 * @param {SerialPort} port
 * @param {string} deviceId
 * @param {(deviceId: string, status: "idle" | "error") => void} onStatus
 * @param {(deviceId: string, type: "connected" | "disconnected" | "open_error" | "close_error" | "read_error") => void} onSystemLog
 */
export const closePort = async (port, deviceId, onStatus, onSystemLog) => {
  try {
    const reader = readerMap.get(deviceId);
    if (reader) {
      await reader.cancel();
      reader.releaseLock();
      readerMap.delete(deviceId);
    }

    if (port.readable) {
      await port.close();
    }

    onStatus(deviceId, "idle");
    //  system 
    onSystemLog(deviceId, "disconnected");

  } catch (e) {
    console.error("serial close failed", e);
    onStatus(deviceId, "error");
    onSystemLog(deviceId, "close_error");
  }
};

/**
 * 
 */
const startReaderTop = async (port, deviceId, onParsed, onStatus, onSystemLog) => {
  const decoder = new TextDecoder("utf-8");
  const reader = port.readable.getReader();
  readerMap.set(deviceId, reader);

  let buffer = "";

  try {
    while (true) {
      const { value, done } = await reader.read();
      if (done) break;

      buffer += decoder.decode(value, { stream: true });
      const lines = buffer.split(/\r?\n/);
      buffer = lines.pop();

      for (const line of lines) {
        const parsed = parseLine(line);
        onParsed(deviceId, parsed);
      }
    }
  } catch (e) {
    console.error("serial read error", e);
    onStatus(deviceId, "error");
    onSystemLog(deviceId, "read_error");
  } finally {
    try {
      reader.releaseLock();
    } catch {}
    readerMap.delete(deviceId);
  }
};
