
export const parseLine = (line) => {
  const trimmed = line.trim();

  // STATE
  const stateMatch = trimmed.match(/^STATE:\s*([A-Za-z0-9_]+)$/);
  if (stateMatch) {
    return {
      kind: "STATE",
      payload: {
        state: stateMatch[1],
      },
    };
  }

  // VAR
  const varMatch = trimmed.match(/^\[VAR:([a-zA-Z_]+)=(.+)\]$/);
  if (varMatch) {
    let value = varMatch[2];
    if (!isNaN(value)) value = Number(value);

    return {
      kind: "VAR",
      payload: {
        key: varMatch[1],
        value,
      },
    };
  }

  // RAW
  return {
    kind: "RAW",
    payload: {
      message: trimmed,
    },
  };
};