import logo from './logo.svg';
// import './App.css';
import './layout.css'
import React, { useState, useEffect} from 'react';
import MermaidChart from './components/MermaidChart';
import FileUploader from './components/LogFileUploader';
import StateLogViewer from './components/StateLogViewer';
import { parseLogs } from './logic/logParser';
import StateJsonFileUploader from './components/StateJsonFileUploader';
import Slider from './components/Slider';
import VarLogger from './components/VarLogger';
import SerialMonitor from './components/SerialMonitor';
import Test from './components/Test';

function App() {
    const [stateLogs, setStateLogs] = useState([]);
    const [varLogs, setVarLogs] = useState([]);
    const [currentIndex, setCurrentIndex] = useState(0);  // 
    const [currentState, setCurrentState] = useState(null); 
    const [stateJson, setStateJson] = useState({});
    const [preState, setPreState] = useState(null);


    // 
    const handleLogLoad = (logText) => {
        const { states, vars } = parseLogs(logText);
        setStateLogs(states);
        setVarLogs(vars);
        if (states.length > 0){
            setCurrentIndex(0);
            setCurrentState(states[0]);
            setCurrentState(states[0]);
        }
    }    

    // JSON
    const handleStateJsonLoad = (json) => {
        setStateJson(json);
        setCurrentState(json.initialState);
    }

    // currentIndexcurrentState
    useEffect(() => {
        if(stateLogs.length > 0){
            const newState = stateLogs[currentIndex];
            setCurrentState(newState);
        }
    }, [currentIndex]);
    
    // 
    const handleNext = () => {
        if (currentIndex < stateLogs.length - 1) {
            // setCurrentState(stateLogs[currentIndex+1]);
            setCurrentIndex(currentIndex + 1);            
        }
    };
    
    // 
    const handlePrevious = () =>{
        if (currentIndex > 0) {
            setCurrentIndex(currentIndex - 1);
        }
    }
  
    // 
    const handleInitial = () => {
        setCurrentIndex(0);
        const initState = stateLogs[0]
        setCurrentState(initState);
    }



  return (
    <div> 
        {/* <Test /> */}
        <h1 className='mb-3' style={{backgroundColor: '#9f8abeff', height: '60px', color:'white', padding:'0px 0px 10px 30px'}}>Log Visualizer</h1>
        <div className="App container">
            <ul className='nav nav-tabs'>   
            <li className='nav-item'>
                <button className='nav-link active' data-bs-toggle="tab" data-bs-target="#tab1">Log Viewer</button>
            </li>
            <li className='nav-item'>
                <button className='nav-link ' data-bs-toggle="tab" data-bs-target="#tab2">Serial Monitor</button>
            </li>
            <li className='nav-item'>
                <button className='nav-link ' data-bs-toggle="tab" data-bs-target="#tab3">HELP</button>
            </li>
            </ul>

            <div className="tab-content">
                <div className="tab-pane active" id="tab1">
                    <div className="uploader-container mb-3">
                        <FileUploader onLoad={handleLogLoad} />
                        <StateJsonFileUploader onLoad={handleStateJsonLoad} />
                    </div>
                    <div className="progress-group" style={{display:'flex', alignItems:'center', margin:'20px'}}>
                        <button
                            onClick={handleNext}
                            disabled={currentIndex >= stateLogs.length - 1}
                            style={{ margin: "10px 10px" }}
                            className="btn btn-primary"
                        >
                             
                        </button>
                        <button
                            onClick={handlePrevious}
                            disabled={currentIndex <= 0}
                            style={{ margin: "10px 10px" }}
                            className="btn btn-danger"
                        >
                            
                        </button>
                        <button
                            onClick={handleInitial}
                            disabled={currentIndex <= 0}
                            style={{ margin: "10px 10px" }}
                            className="btn btn-success"
                        >
                            
                        </button>
                        <div style={{marginLeft :'20px', width: '500px'}}>
                            <Slider logLength={stateLogs.length} currentIndex={currentIndex} setCurrentIndex={setCurrentIndex} />
                        </div>
                    </div>
                    <div className='row'>
                        <div className="col-md-3" style={{padding: '10px'}}>
                            <VarLogger vars={varLogs} currentIndex={currentIndex} />
                        </div>
                        <div className="col-md-3" style={{padding: '10px'}}>
                            <StateLogViewer stateLogs={stateLogs} currentIndex={currentIndex} setCurrentIndex={setCurrentIndex} stateJson={stateJson} />                
                        </div>
                        <div className="col-md-6" style={{padding: '10px'}}  >
                            <MermaidChart currentState={currentState} stateJson={stateJson} currentIndex={currentIndex} stateLogs={stateLogs} className='col-md-6'/>
                        </div>
                    </div>
                </div>
                <div className="tab-pane" id="tab2">
                    <SerialMonitor />
                </div>
                <div className="tab-pane" id="tab3">Tab 3 content</div>
            </div>
        </div>
    </div>    
  );
}

export default App;
