import mermaid from 'mermaid';

// 
function buildMermaidStateDiagramString(stateJson, currentState, currentIndex, stateLogs) {
  try {
    const { states, transitions, initialState } = stateJson;

    // 
    let mermaidString = `
        stateDiagram-v2
        direction TB
        [*] --> ${initialState}
        `;

    // note
    transitions?.forEach(({ from, to } = {}) => {
      if(from && to && !(from === to)) { // 
        mermaidString += `
        ${from} --> ${to}
        `;
      }
    });

    // 
    const preState = stateLogs[currentIndex-1] ?? null; 
    if (preState === currentState) { // 
      mermaidString += `
        style ${currentState} fill:#f9f,stroke:#ffb347,stroke-width:4px;
        `;
    }
    else{
      mermaidString += `
        style ${currentState} fill:#f9f,stroke:#333,stroke-width:2px;
      `;
    }
    // loop
    console.log("========== Debug Info =========");
    console.log("State Logs:", stateLogs);
    console.log("Current Index:", currentIndex);
    console.log("Current State:", currentState);
    console.log("Previous State:", preState);
    console.log("================================");

    
    return mermaidString;
  } catch (error) {
    console.error('<Error parsing state JSON>:', error);
    return '';
  }
}

export default buildMermaidStateDiagramString;
