// snapshot/snapshots.js

// 
// - LogEvent 
// - STATE  VAR 
// - 


/**
 * 
 *
 * :
 * - STATE 
 * - VAR  STATE 
 * - STATE  VAR 
 */
const buildDeviceSnapshots = (deviceId, logs) => {
  const snapshots = [];
  let current = null;

  for (const log of logs) {
    if (log.kind === "STATE") {
      if (current) snapshots.push(current);

      current = {
        deviceId,
        state: log.payload.state,
        stateTime: log.time,
        vars: {},
      };
    }
    else if (log.kind === "VAR") {
      if (!current) continue;
      current.vars[log.payload.key] = log.payload.value;
    }
  }

  if (current) snapshots.push(current);
  return snapshots;
};


/**
 * 
 */
const buildSnapshotsByDevice = (devices) => {
  const result = {};
  for (const device of devices) {
    result[device.id] =
      buildDeviceSnapshots(device.id, device.logs);
  }
  return result;
};


/**
 *  STATE 
 *  stateTime 
 * 
 */
const collectSynchronizationTimes = (snapshotsByDevice) => {
  const times = [];
  for (const deviceId in snapshotsByDevice) {
    for (const snap of snapshotsByDevice[deviceId]) {
      times.push(snap.stateTime);
    }
  }
  return Array.from(new Set(times)).sort((a, b) => a - b);
};


/**
 * 
 *
 * :
 * [
 *   {
 *     t: ,
 *     states: {
 *       deviceId: snapshot | null
 *     }
 *   }
 * ]
 */
const buildSynchronizedTimeline = (devices) => {

  // ---  ---
  //  t 
  const getSnapshotAtTime = (snapshots, t) => {
    let result = null;
    for (const snap of snapshots) {
      if (snap.stateTime <= t) result = snap;
      else break;
    }
    return result;
  };

    //  t 
  const getAllSnapshotsAtTime = (snapshotsByDevice, t) => {
    const result = {};
    for (const deviceId in snapshotsByDevice) {
      result[deviceId] =
        getSnapshotAtTime(snapshotsByDevice[deviceId], t);
    }
    return result;
  };

  // ---  ---

  const snapshotsByDevice = buildSnapshotsByDevice(devices);
  const times = collectSynchronizationTimes(snapshotsByDevice);

  return times.map(t => ({
    t,
    states: getAllSnapshotsAtTime(snapshotsByDevice, t),
  }));
};


// API
export {
  buildSynchronizedTimeline,
};



