import React, { useState, useEffect } from "react";
import "../layout.css"

function StateJsonFileUploader({ onLoad }) {
    const [jsonData, setJsonData] = useState(null);

    const handleFileUpload = (e) => {
        const file = e.target.files[0];
        if(!file) return;
        // 
        const reader = new FileReader();
        // , addEventListener
        reader.onload  = (event) => {
            const content = event.target.result;
            try{
                const jsonData = JSON.parse(content);
                setJsonData(jsonData);
                onLoad(jsonData);
            } catch (error) {
                console.error("Error parsing JSON:", error);   
            }
        };
        // json
        reader.readAsText(file);
        // JSON
    }
    return (
        <div className="mb-3">
            <label htmlFor="jsonFileInput" className="form-label">JSON </label>
            <input type="file" id="jsonFileInput" accept=".json" onChange={handleFileUpload} className="form-control" placeholder="" />
        </div>
    );
}

export default StateJsonFileUploader; 