import React, { useState, useEffect } from "react";

export function parseLogs(rawText) {
    const lines = rawText.split(/\r?\n/); // 
    let states = [];
    let vars = [];
    let lastVars = {}; // 

    for(const line of lines){
        // STATEVAR
        // STATE
        if(isStateLine(line)){
            states.push(getStateFromLine(line))
            vars.push({ ...lastVars });
        }
        // VAR
        else if(isVarLine(line)){
            const { name, value } = getVarFromLine(line);
            lastVars[name] = value;
            if (vars.length > 0) {
                vars[vars.length - 1][name] = value;
            }
        }
    }

  return { states, vars };
}

export function isStateLine(line) {
    return line.startsWith("STATE:");
}

export function isVarLine(line) {
    return line.startsWith("[VAR:");
}

export function getStateFromLine(line) {
    return line.split("STATE:")[1].trim();
}

export function getVarFromLine(line) {
    const match = line.match(/\[VAR:(\w+)=([-\w.]+)\]/);
    return match ? { name: match[1], value: match[2] } : null;
}