import React, { useState, useEffect } from "react";

function FileUploader({ onLoad }) {
    const handleFileUpload = (e) => {
        const file = e.target.files[0];
        console.log("selected file:", file);
        if(!file) return;
        // 
        const reader = new FileReader();
        // , addEventListener
        reader.onload  = (event) => {
            const rawLogText = event.target.result;
            console.log("raw log text:", rawLogText);
            // const parsedLogs = parseLogs(logText);
            onLoad(rawLogText); //
        };
        // 
        reader.readAsText(file);
    }
    return (
        <div>
            <div className="mb-3">
                {/* <span className="input-group-text"></span> */}
                <label className="form-label" htmlFor="logFileInput"></label>
                <input type="file" id="logFileInput" accept=".txt" onChange={handleFileUpload} className="form-control" placeholder="hello" />
            </div>

        </div>
    );
}

export default FileUploader;