import React, { useState, useEffect, useRef } from "react";

function StateLogViewer({ stateLogs, currentIndex, setCurrentIndex }) {
  const [visibleLines, setVisibleLines] = useState(10); // 
  const containerRef = useRef(null); // 
  const lineRefs = useRef([]); // 

  const lineHeight = 22; // (px)

  // currentIndex 
  useEffect(() => {
    if (containerRef.current && lineRefs.current[currentIndex]) {
      const container = containerRef.current; // 
      const line = lineRefs.current[currentIndex]; // 
      const lineTop = line.offsetTop; // (px)
      const lineBottom = lineTop + line.offsetHeight; // (px)

      // 
      if (lineTop < container.scrollTop) {
        container.scrollTop = lineTop - 4; // 
      } else if (lineBottom > container.scrollTop + container.clientHeight) {
        container.scrollTop = lineBottom - container.clientHeight + 4;
      }
    }
  }, [currentIndex]);

  return (
    <div className="card" style={{ minHeight: "300px" }}>
      <div
        className="card-header"
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        <span> Log</span>
        <div style={{ display: "flex", alignItems: "center" }}  >
          <label style={{ marginRight: "2px", whiteSpace: "nowrap" }}></label>
          <select
            className="form-select form-select-sm"
            value={visibleLines}
            onChange={(e) => setVisibleLines(parseInt(e.target.value))}
            style={{ padding: "2px 6px", width: "80px" }}
          >
            <option value={10}>10</option>
            <option value={30}>30</option>
            <option value={50}>50</option>
            <option value={100}>100</option>
          </select>
        </div>
      </div>

      <div
        className="card-body"
        ref={containerRef}
        style={{
          overflowY: "auto",
          maxHeight: `${visibleLines * lineHeight}px`,
          backgroundColor: "#fafafa",
          borderTop: "1px solid #ddd",
          padding: "6px 10px",
          fontFamily: "monospace",
        }}
      >
        <ul style={{ listStyleType: "none", padding: 0, margin: 0 }}>
          {stateLogs.map((line, index) => (
            <li
              key={index}
              ref={(el) => (lineRefs.current[index] = el)}
              onClick={() => setCurrentIndex(index)}
              style={{
                color: index === currentIndex ? "red" : "black",
                cursor: "pointer",
                backgroundColor:
                  index === currentIndex ? "#ffe4b5" : "transparent",
                transition: "background-color 0.2s",
                padding: "1px 0",
              }}
            >
              {line}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}

export default StateLogViewer;
