import React, { useState, useEffect } from 'react';

const VarLogger = ({ vars, currentIndex }) => {
    const [selectedVar, setSelectedVar] = useState([]); // 
    const [varGroups, setVarGroups] = useState(['']); // 

    // vars
    useEffect(() => {
        console.log("Vars in VarLogger:", vars);
        //
        Object.keys(vars?.[vars.length - 1] || {}).forEach(key=>{
            console.log(`Key in VarLogger: ${key}, Value: ${vars[vars.length - 1][key]}`);
        });        
    }, [vars]);

    // selectedVar
    useEffect(()=>{
        console.log("Selected Variable changed:", selectedVar);
    }, [selectedVar])

    // varGroups
    const addVarGroup = () => {
       setVarGroups([...varGroups, {}]);
    }

    return (
        <div className='var-logger card' style={{minHeight: '300px'}}>
            <div className='card-header'> Variables </div>
            <div className="card-body  selected-vars">
                {varGroups.map((_, index) => (
                    <React.Fragment key={index}>
                        <select className="form-select mb-2" onChange={(e)=>setSelectedVar([...selectedVar, e.target.value])} value={selectedVar[index] || ''}>
                            <option value="" disabled></option>
                            {Object.keys(vars?.[vars.length - 1] || {'': ''}).map(key => (
                                <option key={key} value={key} >{key}</option>
                            ))}
                        </select>
                        <div key={index} className="var-group">
                            {/* <p>Variable: {selectedVar[index]}</p> */}
                            <p>Value: {vars?.[currentIndex]?.[selectedVar[index]]}</p>
                            <hr className="border border-primary border-2 opacity-75 my-3" />
                        </div>
                    </React.Fragment>
                ))}
            </div>
            <button className="btn btn-primary" style={{margin: '20px'}} onClick={()=>addVarGroup()} ></button>
        </div>
    );
};

export default VarLogger;
