import React, { useState, useEffect } from "react";

function Slider({ logLength, currentIndex, setCurrentIndex }) {

  const changeRanges = (e) => {
    const newCurrentIndex = Number(e.target.value);
    setCurrentIndex(newCurrentIndex);
    console.log("new slider index: ",newCurrentIndex);
  };

  return (
    <div style={{ backgroundColor: '#efefef', padding: '10px', borderRadius: '5px' }}>
      <label htmlFor="logRange">Log Range</label>
      <input type="range" className="form-range" min={0} max={logLength-1} step={1} value={currentIndex} onChange={(e) => changeRanges(e)} />
    </div>
  );
}

export default Slider;