import { useState, useRef } from "react";
import { openPort, closePort } from "../serial/serialIO";
import { buildSynchronizedTimeline } from "../snapshot/snapshots";

function SerialMonitor() {
  const [devices, setDevices] = useState([]);
  const [nextId, setNextId] = useState(1);
  const readerRefs = useRef({});

  // 
  const addPort = async () => {
    try {
      const port = await navigator.serial.requestPort(); // 
      setDevices(prev => [
        ...prev,
        {
          id: `Node${nextId}`,
          port,
          status: "idle",
          logs: [],
        },
      ]);
      setNextId(prev => prev + 1);
    } catch (e) {
      console.error("", e);
    }
  };

  // 
  const appendLog = (logEvent) => {
    setDevices(prev =>
      prev.map(d =>
        d.id === logEvent.deviceId
          ? { ...d, logs: [...d.logs, logEvent] }
          : d
      )
    );
  };

  // === serialIO  ===
  // 
  const handleParsedLine = (deviceId, parsed) => {
    appendLog({
      deviceId,
      time: performance.now(),
      kind: parsed.kind,
      payload: parsed.payload,
    });
  };

  // 
  const handleStatus = (deviceId, status) => {
    setDevices(prev =>
      prev.map(d =>
        d.id === deviceId ? { ...d, status } : d
      )
    );
  };

  // 
  const handleSystemLog = (deviceId, type) => {
    const messages = {
      "connected": "===  ===",
      "disconnected": "===  ===",
      "open_error": "!!!  !!!",
      "close_error": "!!!  !!!",
      "read_error": "!!!  !!!",
    }

    const message = messages[type];
    if (!message) return;

    appendLog({
      deviceId,
      time: performance.now(),
      kind: "SYSTEM",
      payload: { message },
    });
  }

  // === UI ===
  //  
  const handleOpen = async (device) => {
    try {
      await openPort(device.port, device.id, handleParsedLine, handleStatus, handleSystemLog);
      handleStatus(device.id, "open");
    } catch (e) {
      console.error("open failed", e);
    }
  };

  //   
  const handleClose = async (device) => {
    await closePort(device.port, device.id, handleStatus, handleSystemLog);
    handleStatus(device.id, "idle");
  };


  // 
  const renderLog = (log) => {
    switch (log.kind) {
      case "STATE":
        return `STATE: ${log.payload.state}`;
      case "VAR":
        return `<VAR: ${log.payload.key}=${log.payload.value}>`;
      case "RAW":
        return log.payload.message;
      case "SYSTEM":
        return `*** ${log.payload.message} ***`;
    }
  }



  // UI
  return (
    <div style={{ padding: "12px" }}>
      <button onClick={() => console.log(buildSynchronizedTimeline(devices))}></button>
      <h2> </h2>
      <button onClick={addPort}></button>

      <ul style={{ listStyle: "none", marginTop: "16px", display: "flex" }}>
        {devices.map((dev, idx) => (
          <li key={dev.id} style={{ marginBottom: "8px", marginRight: "24px", border: "1px solid #ccc", padding: "8px", width: "300px" }}>
            <strong>{dev.id}</strong> / : {dev.status}

            {dev.status === "idle" && (
              <button onClick={() => handleOpen(dev)} style={{ marginLeft: "8px" }}>
                
              </button>
            )}

            {dev.status === "open" && (
              <button onClick={() => handleClose(dev)} style={{ marginLeft: "8px" }}>
                
              </button>
            )}

            <section>
              <div style={{ fontFamily: "monospace", whiteSpace: "pre-wrap" }}>
                {dev.logs.map((l, i) => (
                  <div
                    key={i}
                    style={{
                      color: l.type === "system" ? "#888" : "#000",
                      fontStyle: l.type === "system" ? "italic" : "normal",
                    }}
                  >
                    [{l.time.toFixed(1)} ms] {renderLog(l)}
                  </div>
                ))}
              </div>
            </section>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default SerialMonitor;
